class Vehicle {
  constructor(public color: string) {
    this.color = color
  }

  drive(): void {
    console.log('cha ga')
  }

  honk(): void {
    console.log('beep')
  }
}

class Car extends Vehicle {
  constructor(public wheels: number, color: string) {
    super(color)
  }

  getColor(): void {
    console.log(this.color)
  }

  getWheels(): void {
    console.log(this.wheels)
  }
}

const car = new Car(2, 'blue')
car.drive()
car.honk()
car.getColor()
car.getWheels()
